* {
  padding: 0;
  margin: 0;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  font-family: 'Roboto', "맑은 고딕", "Malgun Gothic", dotum;
  ;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul,
ol {
  list-style: none;
}

.container {
  width: 1280px;
  margin: 0 auto;
}

a,
button {
  text-decoration: none;
}

.flex_box {
  display: flex;
  flex-wrap: wrap;
}

.flex_box>* {
  flex: 0 0 auto;
}

.text_center {
  text-align: center;
}

.text_left {
  text-align: left;
}

.text_right {
  text-align: right;
}

.display_none {
  display: none;
}
/* ----------------------------------------------------------
              header  - 추후 해당컴포넌트로 분리하던지..
------------------------------------------------------------*/
i[class*="ico_"] {
  display: inline-block;
}

.ico_s10 {width:10px; height:10px;}
.ico_s20 {width:20px; height:20px;}
.ico_s30 {width:30px; height:30px;}
.ico_s40 {width:40px; height:40px;}
.ico_s50 {width:50px; height:50px;}
/* ----------------------------------------------------------
              header  - 추후 해당컴포넌트로 분리하던지..
------------------------------------------------------------*/
#header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, .1);
  z-index: 100;
  background-color: #fff;
}

#header h1 {
  font-size: 0;
  padding: 10px 0;
}

#header .flex_box {
  justify-content: space-between;
}

.gnbmenu ul li {}

.gnbmenu ul li a {
  position: relative;
  display: block;
  font-size: 18px;
  letter-spacing: -.5px;
  padding: 19px 35px;
  color: #333;
  text-decoration: none;
}

.gnbmenu ul li a:after {
  content: "";
  display: block;
  width: 90%;
  height: 3px;
  background-color: #2388e0;
  transform: scale(0);
  transition: all .5s;
  position: absolute;
  bottom: 0;
  left: 5%;
}

.gnbmenu ul li a.on:after {
  transform: scale(1);
}

/* ----------------------------------------------------------
              header  - 추후 해당컴포넌트로 분리하던지..
------------------------------------------------------------*/
.common_btn_warp {
  position:relative;
  z-index: 5;
}

.common_btn {
  display: inline-block;
  line-height: 38px;
  padding: 0 15px;
  border-radius: 8px;
  background-color: #fff;
  border: 1px solid #ddd;
  color: #333;
  transition: all .4s;
  font-size: 16px;
}
.btn_blue {
  color: #fff;
  background-color: var(--color-blue-5);
  border-color: var(--color-blue-5);
}
.btn_blue:hover {
  color: #fff;
  background-color: var(--color-blue-8);
  border-color: var(--color-blue-8);
}